
'use client';

import { useEffect, useState, useTransition } from 'react';
import { useRouter, useSearchParams } from 'next/navigation';
import LoadingLink from '@/components/loading-link';
import { getSessionUser, getUserProfile, joinEventUsingCdKeyOnlyAction, getAnnouncementsAction } from '@/lib/actions';
import type { User, Event, Announcement } from '@/lib/types'; 
import { Card, CardContent, CardDescription, CardHeader, CardTitle, CardFooter } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import EventCard from '@/components/event-card';
import { Alert, AlertDescription, AlertTitle } from '@/components/ui/alert';
import { Loader2, UserCircle, CalendarDays, Ticket, Home, Info, LogIn, BarChart3, KeyRound, UserPlus, Megaphone, ServerCrash } from 'lucide-react'; 
import { Separator } from '@/components/ui/separator';
import { useLoading } from '@/contexts/loading-context';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { useToast } from '@/hooks/use-toast';
import { format, parseISO } from 'date-fns';
import { zhCN } from 'date-fns/locale';
import { ScrollArea } from '@/components/ui/scroll-area';


interface ProfileData {
  user: User;
  joinedEvents: Event[];
}

export default function DashboardPage() {
  const [profileData, setProfileData] = useState<ProfileData | null>(null);
  const [announcements, setAnnouncements] = useState<Announcement[]>([]);
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [isDebugView, setIsDebugView] = useState(false);
  const router = useRouter();
  const searchParams = useSearchParams();
  const { startLoading, stopLoading } = useLoading();
  const { toast } = useToast();

  const [cdKeyInput, setCdKeyInput] = useState('');
  const [isJoiningDashboardEvent, startJoinDashboardEventTransition] = useTransition();


  const fetchDashboardData = async () => {
    setIsLoading(true);
    setError(null);
    startLoading();

    const debugUserId = searchParams.get('debug_user_id');
    setIsDebugView(!!debugUserId);

    try {
      try {
        const fetchedAnnouncements = await getAnnouncementsAction();
        setAnnouncements(fetchedAnnouncements.sort((a, b) => parseISO(b.date).getTime() - parseISO(a.date).getTime()));
      } catch (announceError) {
        console.error('获取公告失败:', announceError);
        toast({ title: '提示', description: '无法加载最新公告。', variant: 'default' });
      }

      let data: ProfileData | null = null;
      if (debugUserId) {
        data = await getUserProfile(debugUserId);
        if (!data) {
          setError(`无法加载调试用户 ${debugUserId} 的个人资料。该用户可能不存在。`);
        }
      } else {
        const sessionUser = await getSessionUser();
        if (!sessionUser) {
          router.push('/login?redirect=/dashboard');
          return; 
        }
        data = await getUserProfile(sessionUser.id);
        if (!data) {
          setError('无法加载您的仪表盘数据。');
        }
      }
      setProfileData(data);

    } catch (err) {
      console.error('获取仪表盘数据失败:', err);
      setError('加载仪表盘数据时发生错误。');
      setProfileData(null); 
    } finally {
      setIsLoading(false);
       if (typeof window !== 'undefined' && window.location.pathname.startsWith('/dashboard')) {
          stopLoading();
      }
    }
  };

  useEffect(() => {
    fetchDashboardData();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [searchParams]);

  const handleNavigate = (path: string) => {
    startLoading();
    router.push(path);
  };

  const handleJoinEventDashboard = async () => {
    if (!cdKeyInput.trim()) {
      toast({ title: '提示', description: '请输入活动活动代码。', variant: 'default' });
      return;
    }
    startLoading();
    startJoinDashboardEventTransition(async () => {
      try {
        const result = await joinEventUsingCdKeyOnlyAction(cdKeyInput);
        if (result.success) {
          toast({ title: '成功加入！', description: result.message });
          setCdKeyInput('');
          await fetchDashboardData(); 
        } else {
          toast({ title: '加入失败', description: result.message, variant: 'destructive' });
        }
      } catch (e) {
        console.error("加入活动错误（仪表盘）:", e);
        toast({ title: '错误', description: '加入活动时发生未知错误。', variant: 'destructive' });
      } finally {
        stopLoading();
      }
    });
  };


  if (isLoading && !profileData && announcements.length === 0) {
    return (
      <div className="flex justify-center items-center min-h-[calc(100vh-20rem)]">
        <Loader2 className="h-12 w-12 animate-spin text-primary" />
        <p className="ml-4 text-muted-foreground">正在加载仪表盘数据...</p>
      </div>
    );
  }

  if (error && !profileData) { 
    return (
       <div className="container mx-auto px-4 py-8 max-w-4xl">
        <Alert variant="destructive" className="max-w-lg mx-auto my-10">
          <ServerCrash className="h-5 w-5" />
          <AlertTitle>加载仪表盘错误</AlertTitle>
          <AlertDescription>{error}</AlertDescription>
          <Button onClick={() => handleNavigate(isDebugView ? '/login' : '/')} variant="outline" className="mt-4">
            {isDebugView ? <LogIn className="mr-2 h-4 w-4" /> : <Home className="mr-2 h-4 w-4" />}
            {isDebugView ? '返回登录页' : '返回首页'}
          </Button>
        </Alert>
      </div>
    );
  }

  if (!profileData && !isLoading) { 
     return (
      <div className="container mx-auto px-4 py-8 max-w-4xl">
        <Alert className="max-w-lg mx-auto my-10">
          <Info className="h-5 w-5" />
          <AlertTitle>未找到用户数据</AlertTitle>
          <AlertDescription>我们未能加载您的用户配置信息。如果您是新用户，请尝试重新登录。如果您是调试用户，请确保用户ID有效。</AlertDescription>
          <Button onClick={() => handleNavigate(isDebugView ? '/login' : '/login?redirect=/dashboard')} variant="outline" className="mt-4">
            <LogIn className="mr-2 h-4 w-4" /> {isDebugView ? '返回登录页' : '前往登录'}
          </Button>
        </Alert>
      </div>
    );
  }
  
  const user = profileData?.user;
  const joinedEvents = profileData?.joinedEvents.sort((a, b) => parseISO(b.date).getTime() - parseISO(a.date).getTime()) || [];


  if (!user) { 
    return (
         <div className="flex justify-center items-center min-h-[calc(100vh-20rem)]">
            <Alert variant="destructive">
                <AlertTitle>错误</AlertTitle>
                <AlertDescription>无法加载用户信息。请尝试重新登录。</AlertDescription>
                <Button onClick={() => router.push('/login')} className="mt-4">前往登录</Button>
            </Alert>
        </div>
    );
  }


  return (
    <div className="space-y-8">
      {isDebugView && (
        <Alert variant="default" className="border-yellow-500 bg-yellow-50 dark:bg-yellow-900/30 dark:border-yellow-700">
          <Info className="h-5 w-5 text-yellow-700 dark:text-yellow-300" />
          <AlertTitle className="text-yellow-800 dark:text-yellow-200">调试模式</AlertTitle>
          <AlertDescription className="text-yellow-700 dark:text-yellow-300">
            当前正在以调试模式查看用户 <strong>{user.username}</strong> 的仪表盘。
            <Button variant="link" onClick={() => handleNavigate('/dashboard')} className="p-0 h-auto ml-2 text-yellow-700 dark:text-yellow-300 hover:underline">
              清除调试视图并查看本人仪表盘
            </Button>
          </AlertDescription>
        </Alert>
      )}

      <h1 className="text-3xl font-bold text-foreground">欢迎回来, {user.username}!</h1>
      
      {announcements.length > 0 && (
        <section>
          <Card className="shadow-lg border-accent/50">
            <CardHeader>
              <CardTitle className="text-xl font-semibold flex items-center text-accent">
                <Megaphone className="mr-3 h-6 w-6" />
                最新公告
              </CardTitle>
            </CardHeader>
            <CardContent>
              <ScrollArea className="h-40 pr-4"> 
                <ul className="space-y-4">
                  {announcements.map((announce) => (
                    <li key={announce.id} className="pb-2 border-b border-dashed last:border-b-0">
                      <h4 className="font-semibold text-md">{announce.title}</h4>
                      <p className="text-xs text-muted-foreground mb-1">
                        {format(parseISO(announce.date), 'yyyy年MM月d日', { locale: zhCN })}
                      </p>
                      <p className="text-sm text-muted-foreground">{announce.content}</p>
                    </li>
                  ))}
                </ul>
              </ScrollArea>
            </CardContent>
          </Card>
        </section>
      )}


      <div className="grid gap-6 md:grid-cols-2 lg:grid-cols-3">
        <Card className="shadow-lg">
            <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
                <CardTitle className="text-sm font-medium">已加入活动</CardTitle>
                <Ticket className="h-5 w-5 text-primary" />
            </CardHeader>
            <CardContent>
                <div className="text-2xl font-bold">{joinedEvents.length}</div>
                <p className="text-xs text-muted-foreground">个活动您已报名</p>
            </CardContent>
        </Card>
        <Card className="shadow-lg">
            <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
                <CardTitle className="text-sm font-medium">账户类型</CardTitle>
                <UserCircle className="h-5 w-5 text-accent" />
            </CardHeader>
            <CardContent>
                <div className="text-2xl font-bold">{user.op === 1 ? '管理员' : '普通用户'}</div>
                <p className="text-xs text-muted-foreground">您的账户权限级别</p>
            </CardContent>
        </Card>
      </div>


      <Card className="overflow-hidden shadow-xl">
        <CardHeader>
          <CardTitle className="text-2xl font-semibold flex items-center text-primary">
            <BarChart3 className="mr-3 h-6 w-6" />
            我的活动概览
          </CardTitle>
          <CardDescription>查看您已加入的活动，或使用活动代码加入新活动。</CardDescription>
        </CardHeader>

        <CardContent className="p-6 space-y-8">
          <section>
            <Card className="bg-secondary/20 border-primary/50 shadow-md">
                <CardHeader>
                    <CardTitle className="flex items-center text-primary"><UserPlus className="mr-2 h-6 w-6"/> 使用活动代码加入活动</CardTitle>
                    <CardDescription>如果您有活动的CD-Key，可以在这里输入以加入。</CardDescription>
                </CardHeader>
                <CardContent className="space-y-3">
                    <div>
                        <Label htmlFor="cdKeyInputDashboard" className="sr-only">活动代码</Label>
                        <Input
                            id="cdKeyInputDashboard"
                            type="text"
                            placeholder="输入活动活动代码 (CD-Key)"
                            value={cdKeyInput}
                            onChange={(e) => setCdKeyInput(e.target.value)}
                            className="mb-3"
                            disabled={isJoiningDashboardEvent || isLoading}
                        />
                    </div>
                    <Button
                        onClick={handleJoinEventDashboard}
                        disabled={!cdKeyInput.trim() || isJoiningDashboardEvent || isLoading}
                        className="w-full"
                    >
                        {isJoiningDashboardEvent ? <Loader2 className="mr-2 h-4 w-4 animate-spin" /> : <KeyRound className="mr-2 h-4 w-4" />}
                        确认加入
                    </Button>
                </CardContent>
            </Card>
          </section>

          <Separator/>

          <section>
            <h3 className="text-xl font-semibold mb-4 flex items-center">
              <Ticket className="mr-3 h-5 w-5 text-muted-foreground" />
              已加入的活动 ({joinedEvents.length})
            </h3>
            {joinedEvents.length > 0 ? (
              <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                {joinedEvents.map(event => (
                  <EventCard key={event.id} event={event} />
                ))}
              </div>
            ) : (
              <Alert>
                <CalendarDays className="h-4 w-4" />
                <AlertTitle>您尚未加入任何活动</AlertTitle>
                <AlertDescription>
                  {isDebugView ? '此调试用户当前未加入任何活动。' : <span>浏览<LoadingLink href="/" className="text-primary hover:underline font-medium">活动列表</LoadingLink>并加入您感兴趣的活动吧！</span>}
                </AlertDescription>
              </Alert>
            )}
          </section>

          <Separator />

          <section>
            <h3 className="text-xl font-semibold mb-4 flex items-center">
                <UserCircle className="mr-3 h-5 w-5 text-muted-foreground" />
                账户信息
            </h3>
            <div className="space-y-3 text-muted-foreground">
                <p><strong>用户名:</strong> {user.username}</p>
                <p><strong>唯一UID:</strong> {user.id}</p>
                <p><strong>密码:</strong> <span className="italic">******** (为安全起见已隐藏)</span></p>
                <p className="text-xs mt-1">在真实应用中，这里应提供“修改密码”功能。</p>
                <p><strong>头像链接:</strong> <LoadingLink href={user.avatarUrl || '#'} target="_blank" rel="noopener noreferrer" className="text-accent hover:underline break-all">{user.avatarUrl || '未设置'}</LoadingLink></p>
                <p><strong>权限级别:</strong> {user.op === 1 ? '管理员' : '普通用户'}</p>
            </div>
          </section>
        </CardContent>
         <CardFooter className="p-6 border-t">
            <Button variant="outline" className="w-full" onClick={() => handleNavigate('/')}>
                <Home className="mr-2 h-4 w-4" /> 返回首页浏览活动
            </Button>
        </CardFooter>
      </Card>
    </div>
  );
}
